.newsContainer {
    margin-top: 95px;
    .tabGlobalMenu,
    .tabEventMenu {
        cursor: pointer;
    }
    .rightContent {
        position: relative;
        height: 1000px;
        max-height: 1000px;
        border-left: 1px solid rgba(196, 196, 196, 0.6);
        overflow-y: scroll;
        overflow-x: hidden;
        .newsImageCoupon {
            img {
                width: 100%;
                height: 320px;
                border: 1px solid #c4c4c4;
                border-radius: 5px;
            }
        }
        .newsImageDes {
            img {
                width: 100%;
                height: 270px;
                border: 1px solid #c4c4c4;
                border-radius: 5px;
            }
        }
        .subEventContainer {
            min-height: 1000px;
            height: fit-content;
            .subEventTabContainer {
                height: 410px;
                max-height: 410px;
                position: relative;
                .subContentTab {
                    position: absolute;
                    bottom: 0;
                }
            }
            .subEventImage {
                background-image: var(--backgroundImage);
                background-repeat: no-repeat;
                background-size: cover;
                height: 200px;
                position: relative;
                .overlayText {
                    width: 100%;
                    position: absolute;
                    bottom: 0;
                }
            }
        }
    }
    .rightContent::-webkit-scrollbar {
        display: none;
        // width: 10px;
        // border-radius: 10px;
    }
